* {
  font-family: Helvetica, Arial, sans-serif;
  color: white;
}
body {
  padding: 60px;
  background-color: #23242a;
}

input{
  font-size: 1.2rem;
  width: 100%;
  color: grey;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  
}
.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}